@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  /* transition:all 0.2s ease-in-out; */
  /* font-family: 'Roboto', sans-serif; */
  font-family: "Montserrat", sans-serif;
}
*::selection {
  background: #9e9e9e;
}

body {
  width: 100%;
  background: #e5e5e5;
  color: black;
  height: 100%;
}


div.homepage{
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    /* justify-content: center; */
}


header{
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* height: 100px; */
    padding-top:20px;
    padding-bottom: 20px;
}

header img{
    height: 120px;
    width: 100%;
}

div.about{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 350px;
    background-color: #fff;
    color:black;
    margin-top:50px;
    margin-bottom:50px;
    padding-top: 50px;
    padding-bottom: 50px;
    border:3px solid black;
}

div.about h1{
    font-size: 2.875rem;
    color:#424d81;
    margin-bottom: 20px;
}

div.about span{
    padding:20px;
}

div.about span p{
    font-size:20px;
    background-color: #424d81;
    color:white;
    padding:25px 40px;
    font-weight: 500;
    border-radius: 10px;
}

div.about span:hover p{
    background-color: black;
    cursor: pointer;
}

div.list{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 350px;
    background-color: #fff;
    color:black;
    padding-top: 50px;
    padding-bottom: 50px;
    border:3px solid #424d81;
}

div.list .title{
    display: flex;
    align-items: baseline;
}

div.list .title span{
    font-size: 50px;
    color:#cb5d5e;
    font-weight: bold;
    padding-right: 10px;
}

div.list .title h1{
    font-size: 50px;
    color:#424d81;
    font-weight: bold;
    margin-bottom: 20px;
}

div.list .info{
    font-size: 1.25rem;
    font-weight: 300;
    text-align: center;
    /* width: 780px; */
    margin: 9px auto 0;
    margin-bottom: 30px;
}

div.list p.button{
    font-size:20px;
    background-color: #db545a;
    
    padding:18px 50px;
    font-weight: 500;
    
}

div.list p.button a{
    font-size: 1.125rem;
    text-decoration: none;
    color:white;
    text-transform: uppercase;
}

div.list p.button{
    cursor: pointer;
}

div.faqs{
    /* display: flex; */
    /* flex-direction: column; */
    /* align-items: center; */
    /* justify-content: center; */
    padding-top: 50px;
    padding-bottom: 200px;
    background-color: #fff;
}

div.faqs .title{
    text-align: center;
    color:#424d81;
    margin-bottom: 60px;
}

div.market{
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center;

}

div.questions-and-answers{
    padding:0 90px
}

div.questions-and-answers .head{
    display: flex;
    justify-content: space-between;
    padding:40px 0;
    border-bottom: 2px solid grey;
}

div.questions-and-answers .head p{
    font-weight: 700;
    font-size: 1.2rem;
    color:black;
}
div.questions-and-answers .head i{
    font-weight: 700;
    font-size: 1.5rem;
}

div.questions-and-answers p.answer{
    margin-top:50px;
}

div.questions-and-answers .head:hover{
    border-bottom: 2px solid black;
    cursor: pointer;
}

/* footer{
    position: absolute;
    bottom: 0;
    right: 0;
    left:0;
} */
div.contact-us{
    height: 200px;
    display: flex;
    justify-content: space-between;
    padding: 20px 90px;
    align-items: center;
    background-color: white;
}

div.left h1{
    font-size: 40px;
    margin-bottom: 20px;
    color:#424d81;
}

div.left p{
    font-size: 20px;
}

div.right i{
    font-size: 70px;
    padding-left:15px ;
}

div.bottom{
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center ;
    background-color: #424d81;
    position: relative;
}

div.bottom p{
    color: white;
    font-size: 25px;
}

div.bottom i{
    position: absolute;
    bottom: 20px;
    color:white;
    right: 20px;
    font-size: 50px;
    border: 2px solid white;
    border-radius: 50%;
    padding: 20px;
}
div.bottom i:hover{
    cursor: pointer;
}



@media screen and (max-width: 768px){
    div.homepage{
        /* display: flex; */
        /* flex-direction: column; */
        width: 100%;
        /* align-items: center; */
        /* justify-content: center; */
    }
    
    header img{
        height: 50px;
    }

    div.left p{
        font-size: 13px;
    }

    div.right i{
        font-size: 20px;
    }
}

@media screen and (max-width: 425px){
    div.about{
      
        height: 200px;
        margin-top:30px;
        margin-bottom:30px;
     
    }
    
    div.about h1{
        font-size: 20px;
    }
    div.about span p{
        font-size:14px;
    }

    div.list .title span{
        font-size: 20px;
        
    }

    div.list .title h1{
        font-size: 20px;
        
    }
    div.list .info{
        font-size: 15px;
        font-weight: 300;
        
    }

    div.faqs .title{
        font-size: 18px;
        margin-bottom: 10px;
    }

    div.faqs{
        
        padding-bottom: 50px;
    }
    div.questions-and-answers{
        padding:0 50px
    }
    div.questions-and-answers .head p{
        font-size: .8rem;
    }

    div.questions-and-answers .head i{
        font-size: 1rem;
    }
    div.questions-and-answers p.answer{
        font-size: 13px;
    }

    div.contact-us{
        
        flex-direction: column;
        
    }

    div.left h1{
        font-size: 30px;
        text-align: center;
    }
    
    div.left p{
        font-size: 15px;
        text-align: center;
        margin-bottom: 10px;
    }
    
    div.right i{
        font-size: 30px;
    }

    div.bottom{
        height: 100px;
     
    }
    div.bottom p{
        font-size: 18px;
    }

    div.bottom i{
        font-size: 18px;
        padding: 10px;
        bottom: 30px;
    }
}

@media screen and (max-width: 320px){
    div.about h1{
        font-size: 18px;
    }
    div.about span p{
        font-size:15px;
        padding:20px 30px;
    }

    div.list .info{
        font-size: 13px;
        font-weight: 300;
        
    }

    div.right i{
        font-size: 20px;
    }

    div.bottom p{
        font-size: 13px;
    }
}
section.loading-screen {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.705);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

#loading {
  display: inline-block;
  width: 50px;
  height: 50px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: #fff;
  animation: spin 1s ease-in-out infinite;
  -webkit-animation: spin 1s ease-in-out infinite;
  justify-content: center;
  align-self: center;
}

.loader-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 600px;
}
@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}

div.message {
  background-color: aliceblue;
  /* width: 70%; */
  padding: 20px;
  text-align: center;
  border-radius: 10px;
}

div.message h3 {
  font-size: 15px;
}

a {
  text-decoration: none;
}
div.message p {
  background-color: #db545a;
  display: inline-block;
  padding: 7px;
  margin-top: 10px;
  color: #ffff;
}
div.message p a {
  color: #fff;
}
@media screen and (max-width: 400px) {
  div.message {
    width: 80%;
  }
}
@media screen and (max-width: 375px) {
  div.message {
    width: 70%;
  }
  div.message h3 {
    font-size: 10px;
  }
}

.wallets{
    background-color: white;
}


.banner{
    /* margin-top: 150px; */
    /* margin: auto; */

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border:5px solid #424d81;
    padding: 50px 0;
    margin: 0px 10% 0 10%;
    border-radius: 5px;
}

.banner h1{
    text-align: center;
    font-size: 50px;
    background-color: #e5e5e5;
    padding: 20px 100px;
    color:#424d81;
    border: 3px solid black;
    /* margin: 40px 0; */
}

.banner .info1{
    margin: 30px 0;
    font-size: 25px;
    text-align: center;
    width: 60%;
    font-weight:bold;
    color:#424d81;
    
}

.banner .info2{
    text-align: center;
    font-size: 20px;
}

.all-wallets{
    margin:70px 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    /* align-items: center; */
    justify-items: center;
    grid-gap: 25px 0px;
}

.box{
    border: 4px solid #424d81;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    justify-content: center;
    padding: 40px 30px;
}
.box img{
    width: 40%;
}
.box span{
    text-transform: uppercase;
    margin:10px 0;
    font-size: 25px;
    font-weight: bolder;
    color: #424d81;
}

.box p{
    text-transform: uppercase;
    background-color: #424d81;
    color:white;
    padding: 20px 60px;
    border-radius: 10px;
}

.box p:hover{
    cursor: pointer;
}


@media screen and (max-width: 768px){

    .banner{
    
    
        padding: 50px 0;
        margin: 0px 1% 0 1%;
        border-radius: 5px;
    }
    .banner h1{
        font-size: 15px;
        padding: 20px 40px;
        /* margin: 40px 0; */
    }

    .banner .info1{
        font-size: 15px;
        width: 90%;
        
    }
    .banner .info2{
        
        font-size: 13px;
    }


    .all-wallets{
        grid-template-columns: repeat(2, 50%);
        
    }
    .box{
        width: 90%;
        justify-content: center;
        padding: 40px 30px;
    }
    .box span{
        font-size: 10px;
    }

    .box p{
        font-size: 13px;
        padding: 10px 10px;
    }
}




@media screen and (max-width: 320px){
    
    .all-wallets{
        grid-template-columns: repeat(1, 1fr);
        
    }
    .box span{
        font-size: 15px;
    }
    
}
section.popup{
    position: fixed;
    background: #101112b5;
    /* height: 100%; */
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center
}

section.popup>div{
    background: #ffffff;
    height: 70%;
    width: 50%;
    border-radius: 15px;
    color:black;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

span svg{
    width: 1.5rem;
    height: 1.5rem;
    color: #c2b6b6;
    
}

div.form-header{
    /* display: flex; */
    text-align: center;

}
div.form-header img{
    width: auto;
    height: 50px;
    margin-right: 15px;
    
}

div.form-header h1{
    font-weight: bold;
    margin-bottom: 40px;
    font-size: 2.125rem;
    line-height: 1.5rem;
    color: #424d81
}

div.form-header p{
    font-weight: bold;
    margin-bottom: 20px;
    font-size: 1.125rem;
    line-height: 1.5rem;
    color: #424d81
}

div.form-header span{
    color: #424d81;
}

p.error-success-msg{
    text-align: center;
    color:#c73d3d;
    font-weight:300
}
div.form{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 19px;
}

form div{
    display: flex;
    flex-direction: column;
    /* align-items: center */
    margin-bottom: 15px;


}

form div label{
    margin-bottom: 5px;
}

form div input{
    height: 30px;
    padding: 20px 0px 20px 40px;
    border-style: solid;
    border-radius: 6px;
    color: black;
}

span.input{
    position: absolute;
    transform: translate(50%, 120%);
}

div.recovery{
    margin-bottom: 5px;
}
form{
    width:100%;
}
form div textarea{
    max-width: 100%;
    min-width: 100%;
    height: 120px;
    border-style: solid;
    border-radius: 6px;
    padding: 20px 20px 20px 40px;
}

span.textarea{
    position: absolute;
    transform: translate(50%, 240%);
}

form h6{
    font-size: 12px;
    font-weight: 300;
}

form h6 i{
    margin-right: 5px; 
}

div.proceed-button{
    width: 30%;
    display: flex;
    /* align-self: center; */
    background:#424d81;
    border-radius: 50px;
    border: 10px solid #7488ef;
    flex-direction: row;
    align-items: center;
    justify-items: center;
    justify-content: center;
    padding: 20px;
    margin: 13px auto;
    color:white;
    text-transform:uppercase;
}
div.proceed-button  svg{
    color:white;
    width: 1.2rem;
    height: 1.5rem;
    margin-left: 10px;
}

div.disabled{
    background: #5e83a0;
    cursor: none!important;
}
div.proceed-button:hover{
    cursor: pointer;
    background: #5e83a0;
    transition: all 0.5s ease-in;
}    

div.cancel{
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
    margin-top: 10px;
}
div.cancel p:hover{
    cursor: pointer;
    background: #c73d3d;
    transition: all 0.5s ease-in;
}

div.disabled-cancel p{
    background: #c73d3d;
    cursor: none!important;
}

div.cancel p{
    background-color: #e53e3e;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    padding-top: .75rem;
    padding-bottom: .75rem;
    border-radius: 6px;
    color: white;
}

div.success-message-wrapper{
    height: 50%;
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
}

div.success-message{
    display:flex;
    flex-direction:column;
    text-align: center;
    justify-content: center;
    align-items: center;
   
    /* height:200px; */
  
}

div.success-message img{
    height: 100px;
    width: auto;
}

div.success-message h1{
    font-weight: 900;
    margin: 20px 0;
    color: #ba6d09;
}

div.success-message h4{
    margin-bottom: 20px;
    font-weight: 300;
    color: #79582d;
}

div.success-message p{
    background: #0c0c0c;
    color: burlywood;
    padding: 10px 10px;
    border-radius: 6px;
    cursor:pointer;
}

@media screen and (max-width: 1024px){
    section.popup>div{
        width:50%;
        height: auto;
    
    }

    div.form-header h1{
        margin-bottom: 5px;
        font-size: 16px;
    }

    div.form-header p{
        margin-bottom: 3px;
        font-size: 10px;
    }

    div.proceed-button{
        width: 60%;
        
    }
}

@media screen and (max-width: 768px){
    section.popup>div{
        width:60%;
    
    }

   

   

    div.form-header span{
        font-size: 10px;
    }

    
}

@media screen and (max-width: 425px){
    section.popup>div{
        width:90%;
    
    }

    
    p.error-success-msg{
        font-size: 14px;
    }
}


@media screen and (max-width: 375px){
    div.proceed-button{
        
        padding: 5px;
        
    }

    div.proceed-button p{
        
        font-size: 14px;
        
    }

    div.cancel p{
       
        padding-left: 1.2rem;
        padding-right: 1.2rem;
        padding-top: .75rem;
        padding-bottom: .75rem;
        
    }
}

@media screen and (max-width: 320px){
    section.popup>div{
        width:100%;
        border-radius: 0px;
        height: 100%;
    }

    

    
}
